window.dotDevMode = 'localhost'; // 'localhost','production','weatest'
if (window.dotDevMode === 'localhost') {
    window.debugToken = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJFUzUxMiJ9.eyJ1aWQiOiIrNzQ2NjEwMjgyMzEiLCJ2ZXIiOjEsImFwcGlkIjoiODQ4ZTdhMTAxODQzZDk0YTAzNDUiLCJzY29wZSI6WyJOYW1lUmVhZCIsIkVtYWlsUmVhZCJdLCJleHAiOjE2NzcxNDkyMzIsImlhdCI6MTY3NzE0NTYzMiwiZGlkIjoyfQ.AP9-barIFFuea4QcrqcA6HqgwFjhpJaRtIX0YIY-hYljOOO6LcENEew2OE8EgESVKcECR9c7YBoLJCQmgh7sKscsAfi4AaYeeTYT_x2YwON6c55qwW71o-8MSdAXXD2AT_1pwjFHkxjP6vKhi1pmPLoudmJwaWY-R58P_l5dmqpFznPZ';
}

const isWeaAgent = () => {
    return navigator.userAgent.includes('Wea');
}

const getMSToken = async () => {
    const getTokenUrl = getHttpRootURL() + 'v1/get-token?t=' + Date.now();
    let res;
    try {
        res = await axios({
            method: 'post',
            url: getTokenUrl,
            headers: {
                "Content-Type": "application/json",
                Authorization: window.token || window.debugToken
            }
        });
    } catch (e) {
        console.error('get token failed', JSON.stringify(e));
        return null;
    }

    if (res?.status === 200 && res?.data) {
        return res.data;
    }
    console.error('get token failed, unknown error!');
    return null;
}

function App() {
    const [page, setPage] = useState('loading');

    useEffect(() => {
        console.log('version: 2023022301');
        if (!isWeaAgent()) {
            console.log('Not in Wea');
            // localhost模式直接显示会议列表
            if (window.dotDevMode === 'localhost') {
                getMSToken().then(res => {
                    if (res) {
                        window.msToken = res;
                        setPage('mainPage');
                    }
                });
                return;
            }
            setPage('badAgent');
            return;
        }

        try {
            // get mini program token
            window.setupWKWebViewJavascriptBridge(function (bridge) {
                bridge.callHandler('getMiniProgramToken', {}, async res => {
                    if (res && res.status === 200 && res.data && res.data.token) {
                        console.log('getMiniProgramToken success!');
                        window.token = res.data.token;
                        // 请求微软token
                        const msToken = await getMSToken();
                        if (msToken) {
                            console.log('getMSToken success!');
                            window.msToken = msToken;
                            setPage('mainPage');
                        } else {
                            console.error('getMSToken failed!');
                            setPage('tokenError');
                        }
                        return;
                    }
                    console.error('getMiniProgramToken failed:', res);
                    setPage('tokenError');
                })
            });
        } catch (e) {
            console.error('js bridge catch error', e)
        }
    }, [])

    if (page === 'loading') {
        return (
            <div className="App">
                <div className="Loading">
                    <Spin size="large"/>
                </div>
            </div>);
    }

    if (page === 'badAgent') {
        return (<div className="App">
            <div className="center-container">
                <div style={{padding: 20}}>
                    <object aria-label="error" data={fatalErrorSvg} style={{height: 120, width: 120}}/>
                    <h3>{"You have no access to open the APP."}</h3>
                </div>
            </div>
        </div>);
    }

    if (page === 'tokenError') {
        return (<div className="App">
            <div className="center-container">
                <div style={{padding: 20}}>
                    <object aria-label="error" data={fatalErrorSvg} style={{height: 120, width: 120}}/>
                    <h3>Get Wea token failed, please try again</h3>
                </div>
            </div>
        </div>);
    }

    if (page === 'mainPage') {
        return <MainPage/>
    }

    return (<div className="App">
        <h3 style={{margin: 20, color: 'red'}}>Fatal error, please contact WeaBot</h3>
    </div>);
}

export default App;
